.i-linebox-container{
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: #000;
	overflow: hidden;
	z-index: 9999;
}
.i-linebox-middle{
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-50%);
}
.i-linebox{
	position: relative;
	height: 80px;
	width: 80px;
	animation: lineBoxRotate 3s infinite cubic-bezier(0, 0.47, 1, 1);
	animation-delay: 4s;
}
@keyframes lineBoxRotate{
	100%{
		transform: rotate(360deg);
	}
}
.i-linebox li:nth-child(1){
	position: absolute;
	top: 0;
	left: 0;
	width: 0;
	height: 5px;
	background-color: teal;
	animation: lineBox1 1s forwards linear;
}
@keyframes lineBox1{
	100%{
		width: 100%;
	}
}
.i-linebox li:nth-child(2){
	position: absolute;
	top: 0;
	right: 0;
	width: 5px;
	height: 0;
	background-color: teal;
	animation: lineBox2 1s forwards linear;
	animation-delay: 1s;
}
@keyframes lineBox2{
	100%{
		height: 100%;
	}
}
